.section {
  padding-top: 40px;

  @include min-sm {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}

.section--last {
  padding-bottom: 0;
}

.section__description {
  margin: 0 0 25px;
  font-size: 12px;

  @include min-sm {
    max-width: 640px;
    margin: 0 auto 60px;
    font-size: 18px;
    text-align: center;
  }
}
