html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  color: #505050;
  font-family: "Noto Sans", Arial, sans-serif;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

svg path {
  fill: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

figure {
  margin: 0;
  position: relative;
}

@media (min-width: 768px) {
  .outline::before {
    content: "";
    position: absolute;
    top: -10px;
    bottom: 10px;
    left: -10px;
    right: 10px;
    z-index: -1;
    display: block;
    border: 1px solid #505050;
  }
}

@media (min-width: 768px) {
  .outline--right::before {
    top: -10px;
    bottom: 10px;
    left: 10px;
    right: -10px;
  }
}

